
import Divider from 'vux/src/components/divider/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
components: {
Divider,
CellFormPreview,
Group,
XButton
},
data() {
return {

sections: [],

decrypts: [],

currentNodeID: null,

currentNodeName: null,

canDoDecrypt: false,

isDisabled: false,

showTip: false,

showCountDown: false,

tipMsgContent: "",

countDowmTimer: null,

countDowmContent: ""
};
},
props: {
endTime: {
type: String,
default: ""
},
endText: {
type: String,
default: ""
},
callback: {
type: Function,
default: ""
}
},
mounted: function() {

this.onResume();

this.onDeviceready();

this.currentNodeID = this.$route.query.nid;

this.currentNodeName = this.$route.query.nodeName;

this.getDecryptStatus();

this.getSectionInfos();

this.getBidderDecryptInfos();
},

destroyed: function() {

clearInterval(this.countDowmTimer);

document.removeEventListener("resume", this.refreshCountDownContent);

okapp.unsubscribe("racloud_callback");
okapp.unsubscribe("racloud_cancel");
},
methods: {

onResume() {


document.addEventListener("resume", this.refreshCountDownContent);
},

refreshCountDownContent() {
this.showCountDown = false;
this.showTip = false;
this.canDoDecrypt = false;
if (null != this.countDowmTimer) {

clearInterval(this.countDowmTimer);
this.countDowmTimer = null;
}

this.getDecryptStatus();
},
onDeviceready() {
var self = this;

okapp.subscribe("racloud_callback", function(eventData) {
console.info(
"universalLinks url :" +
eventData.url +
" " +
JSON.stringify(eventData.params)
);
okapp.getCertAndUserInfoFromRacloud(eventData.params.order_id, function(
userinfo,
certinfo
) {
var keyName = certinfo.KEYNAME;
var loginName = certinfo.LOGINNAME;
var certData = certinfo.CERTDATA;
var serial = certinfo.SERIAL;
var issuerType = certinfo.ISSUER_TYPE;
var tipContent =
"[" +
keyName +
"(" +
loginName +
")]?";
self.$vux.confirm.show({
title: "",
content: tipContent,
confirmText: "",
cancelText: "",

onCancel() {

self.isDisabled = false;
},
onConfirm() {
self.doVerifyCert(certData, serial, issuerType);
}
});




});
});

okapp.subscribe("racloud_cancel", function(eventData) {
console.info(
"universalLinks url :" +
eventData.url +
" " +
JSON.stringify(eventData.params)
);

self.isDisabled = false;
});
},

doVerifyCert(certData, serial, issuerType) {
var self = this;

let url = "/mobile/decrypt/parse";
var data = {
CERT_DATA: certData,
CERT_SERIAL: serial,
ISSUER_TYPE: issuerType
};
let opts = {
timeout: 180,

method: "post",
data: data
};

this.$vux.loading.show({
text: ",...."
});
this.$request.sendRequest(
url,
opts,

function(rs, response) {
self.$vux.loading.hide();
self.doDecrypt();
},

function(error, response) {

self.isDisabled = false;
self.$vux.loading.hide();
AlertModule.show({
title: "",
content: error.errorDesc
});
}
);
},

doAuthentication() {
this.isDisabled = true;
okapp.loginRaCloud();
},

getSectionInfos() {
var self = this;

let url = "/mobile/decrypt/sections";
this.$request.doGet(url, function(rs, response) {

if (null == rs || rs.length < 1) {

self.$vux.toast.show({
type: "text",
text: "!",
time: 2000,
isShowMask: true
});
return;
}


var tpinfo = self.$eokb.getTenderProjectInfo();

var datas = new Array();
for (var i = 0; i < rs.length; i++) {


if (1 == tpinfo.N_IS_SECTION_GROUP) {
let data = [
{
label: "",
value: rs[i].V_BID_SECTION_GROUP_CODE
},
{
label: "",
value: rs[i].V_BID_SECTION_NAMES
},
{
label: "",
value: self.formatSectionStatus(rs[i].V_BID_OPEN_STATUS)
}
];
datas.push(data);
continue;
}

let data = [
{
label: "",
value: rs[i].V_BID_SECTION_NAMES
},
{
label: "",
value: self.formatSectionStatus(rs[i].V_BID_OPEN_STATUS)
}
];
datas.push(data);
}
self.sections = datas;
});
},

getDecryptStatus() {
this.showCountDown = false;
this.showTip = false;
this.canDoDecrypt = false;
var self = this;

let url = "/mobile/decrypt/status/" + this.currentNodeID;
this.$request.doGet(url, function(rs, response) {

var currentTime = rs.CURRENT_TIME;

var decryptEndTime = rs.DECRYPT_END_TIME;

var decryptStatus = rs.DECRYPT_STATUS;

var bidOpenMsg = rs.BID_OPEN_MSG;
if (decryptStatus == 0) {

self.tipMsgContent = "!";
self.showTip = true;
self.canDoDecrypt = false;
} else if (decryptStatus == 1) {

self.tipMsgContent = "";

self.countdowm(currentTime, decryptEndTime);
self.showCountDown = true;
self.showTip = true;
self.canDoDecrypt = true;
} else if (decryptStatus == 2) {

self.tipMsgContent = "!";
self.showTip = true;
self.canDoDecrypt = false;
} else if (decryptStatus == -1) {

self.tipMsgContent = "!";
self.showTip = true;
self.canDoDecrypt = false;
} else if (decryptStatus == 3) {

self.tipMsgContent = "!";
self.showTip = true;
self.canDoDecrypt = false;
}
});
},

getBidderDecryptInfos() {
var self = this;

let url = "/mobile/decrypt";
this.$request.doGet(url, function(rs, response) {

if (null == rs || rs.length < 1) {







return;
}


var tpinfo = self.$eokb.getTenderProjectInfo();

var datas = new Array();
for (var i = 0; i < rs.length; i++) {


if (1 == tpinfo.N_IS_SECTION_GROUP) {
let data = [
{
label: "",
value: rs[i].V_BID_SECTION_GROUP_CODE
},
{
label: "",
value: rs[i].V_BID_SECTION_NAME
},
{
label: "",
value: rs[i].V_BIDDER_NAME
},
{
label: "",
value: rs[i].V_TIPSNAME
},
{
label: "",
value: self.formatDecryptStatus(rs[i].N_DECRYPT_STATUS)
}
];
datas.push(data);
continue;
}

let data = [
{
label: "",
value: rs[i].V_BID_SECTION_NAME
},
{
label: "",
value: rs[i].V_BIDDER_NAME
},
{
label: "",
value: rs[i].V_TIPSNAME
},
{
label: "",
value: self.formatDecryptStatus(rs[i].N_DECRYPT_STATUS)
}
];
datas.push(data);
}
self.decrypts = datas;
});
},

countdowm(start, end) {
let self = this;

let nowTime = new Date(start - 1000);

let endTime = new Date(end);
this.countDowmTimer = setInterval(function() {
let t = endTime.getTime() - nowTime.getTime();


nowTime = new Date(nowTime.getTime() + 1000);
if (t > 0) {
let day = Math.floor(t / 86400000);
let hour = Math.floor((t / 3600000) % 24);
let min = Math.floor((t / 60000) % 60);
let sec = Math.floor((t / 1000) % 60);
hour = hour < 10 ? "0" + hour : hour;
min = min < 10 ? "0" + min : min;
sec = sec < 10 ? "0" + sec : sec;
let format = "";
if (day > 0) {
format = `${day}${hour}${min}${sec}`;
}
if (day <= 0 && hour > 0) {
format = `${hour}${min}${sec}`;
}
if (day <= 0 && hour <= 0) {
format = `${min}${sec}`;
}
self.countDowmContent = format;
} else {
clearInterval(self.countDowmTimer);
self.countDowmTimer = null;
self.canDoDecrypt = false;
self.countDowmContent = self.endText;

self.getDecryptStatus();

self.getBidderDecryptInfos();
self._callback();
}
}, 1000);
},
_callback() {
if (this.callback && this.callback instanceof Function) {
this.callback(...this);
}
},
formatSectionStatus(val) {
if (val == "0") {
return "<span style='color:#DC143C;font-weight: bold;'></span>";
} else if (val.indexOf("10-") == 0) {
return "<span style='color:#DC143C;font-weight: bold;'></span>";
} else {
return "<span style='color:#3CB371;font-weight: bold;'></span>";
}
return val;
},
formatDecryptStatus(val) {
if (val == 0) {
return "<span></span>";
} else if (val < 30) {
return "<span style='color:red;font-weight: bold;'></span>";
} else if (val >= 30) {
return "<span style='color:green;font-weight: bold;'></span>";
}
},

doDecrypt() {
var self = this;

this.$vux.loading.show({
text: ",...."
});

let url = "/mobile/decrypt/excute";
let opts = {
timeout: 180,

method: "post"
};
this.$request.sendRequest(
url,
opts,

function(rs, response) {

self.isDisabled = true;
self.$vux.loading.hide();

clearInterval(self.countDowmTimer);
self.countDowmTimer = null;
self.canDoDecrypt = false;

self.getDecryptStatus();

self.getBidderDecryptInfos();
var message = self.$eokb.getCurrentUserInfo().V_NAME + "";
var param = new Object();
param.msg = message;
param.nodeID = self.currentNodeID;
param.title = self.currentNodeName;

self.$im.sendReplyTendererCommond(param);
},

function(error, response) {

self.isDisabled = false;
self.$vux.loading.hide();
AlertModule.show({
title: "",
content: error.errorDesc
});
}
);
}
}
};



