
import FormPreview from 'vux/src/components/form-preview/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
components: {
FormPreview,
LoadMore,
Search,
XButton,
Confirm,
dateFormat
},
data() {
return {
groups: [],
button: [
{
style: "primary",
text: "",
onButtonClick: name => {
this.$router.push({
path: "/bidderinfo",
query: {
bidderID: name,
index: this.$route.params.index,
tableName: "EKB_T_DECRYPT_INFO"
}
});
}
}
],
iptValue: "",
showConfirmButton: null,

currentNodeID: null,

currentNodeName: null,

currentNodeStatus: null,

showTip: null,

countDowmTimer: null,

confirmTime: null
};
},
mounted: function() {

this.onResume();

this.getCredits();

this.getConfirmStatus();

this.currentNodeID = this.$route.query.nid;

this.currentNodeName = this.$route.query.nodeName;

this.currentNodeStatus = this.$route.query.nodeStatus;
},

destroyed: function() {

clearInterval(this.countDowmTimer);

document.removeEventListener("resume", this.refreshCountDownContent);

okapp.unsubscribe("racloud_callback");
okapp.unsubscribe("racloud_cancel");
},
methods: {

onResume() {


document.addEventListener("resume", this.refreshCountDownContent);
},

refreshCountDownContent() {
this.showTip = false;
if (null != this.countDowmTimer) {

clearInterval(this.countDowmTimer);
this.countDowmTimer = null;
}

this.getConfirmStatus();
},
getCredits() {
var self = this;
self.$vux.loading.show({
text: "..."
});
let options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID(),
bidderName: self.iptValue
}
};
this.$request.sendRequest("/mobile/bidder/creditlist", options, function(
rs,
response
) {
self.$vux.loading.hide();
self.groups = rs;
});
},
onSubmit() {
this.$refs.search.setBlur();
this.getCredits();
},
doConfirm(status, bidderID, groupCode) {
let self = this;
if (2 == status) {
self.$vux.confirm.prompt("", {
title: "",
confirmText: "",
cancelText: "",


onCancel() {

},
onConfirm(val) {
if (typeof val == "undefined" || val == null || val == "") {
alert("");
return;
}
val = "" + val;
self.confirm(status, bidderID, val, groupCode);
}
});
} else {
self.confirm(status, bidderID, "", groupCode);
}
},
confirm(status, bidderID, remark, groupCode) {
let self = this;
self.$vux.loading.show({
text: "..."
});
let options = {
method: "post",
data: {
status: status.toString(),
tpID: self.$eokb.getTenderProjectID(),
modelType: "DYXF_credit",
bidderID: bidderID,
remark: remark,
groupCode: groupCode
}
};
self.$request.sendRequest("/mobile/modelCheck/confirm", options, function(
rs,
response
) {
var contennt = "";
if (status == 2) {
contennt = "";
}
var message = self.$eokb.getCurrentUserInfo().V_NAME + "-" + contennt;
var param = new Object();
param.nodeID = self.currentNodeID;
param.title = self.currentNodeName;
param.msg = message;

self.$im.sendReplyTendererCommond(param);
self.$router.push("/turningPage");
});
},
getConfirmStatus() {
var self = this;
self.$vux.loading.show({
text: "..."
});
let options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID(),
modelType: "DYXF_credit"
}
};
self.$request.sendRequest("/mobile/modelCheck/status", options, function(
rs,
response
) {
self.$vux.loading.hide();

self.showTip = rs.success;
self.confirmTime = dateFormat(
rs.result.N_CONFIRM_TIME,
"YYYY-MM-DD HH:mm:ss"
);

if (
rs.result.NOW_TIME < rs.result.N_CONFIRM_TIME &&
self.currentNodeStatus == 2
) {
self.countdowm(rs.result.NOW_TIME, rs.result.N_CONFIRM_TIME);
}
});
},

countdowm(start, end) {
let self = this;
let timer = setInterval(function() {
let t = end - start;


start = start + 1000;
if (t <= 0) {
clearInterval(self.countDowmTimer);
self.$command.flushFlowPage();
}
}, 1000);
}
}
};



