
import FormPreview from 'vux/src/components/form-preview/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import Scroll from "./Scroller";
import axios from "axios";
export default {
data() {
return {
page: 1, //
size: 3, // 
rows: [], // 
loop: null,
showMenus: false,
ouid: "",
buttons: [
{
style: "primary",
text: "",
onButtonClick: name => {
this.$eokb.setTenderProjectID(`${name.ID}`);
this.doAuthentication();


}
}
]
};
},
mounted: function() {

this.$session.clear();
this.getList();
},
methods: {
getList() {
var self = this;
self.$vux.loading.show({
text: "..."
});
const options = {
method: "get",
params: { page: self.page.toString(), size: self.size.toString() },

headers: {}
};
this.$request.sendRequest(
"/mobile/project/list",
options,
function(rs, response) {

self.$vux.loading.hide();

if (rs.totalElements == 0) {

self.$el.querySelector(".load-more").style.display = "none";
self.$el.querySelector("#load-msg").style.display = "block";
return;
}
if (rs.totalElements <= self.size) {
self.$el.querySelector(".load-more").style.display = "none";
}

self.$el.querySelector("#load-msg").style.display = "none";
self.rows = rs.content;
},
function(response) {

self.$vux.loading.hide();
self.$el.querySelector(".load-more").style.display = "none";
self.$el.querySelector("#load-msg").style.display = "block";
okapp.alert({ message: response.errorDesc });
}
);
},
onRefresh(done) {
this.getList();
done(); 
},
onInfinite(done) {
let self = this;
const options = {
method: "get",
params: { page: (self.page + 1).toString(), size: self.size.toString() }
};
this.$request.sendRequest(
"/mobile/project/list",
options,
function(rs, response) {


if (self.page >= rs.totalPages) {

self.$vux.toast.show({
type: "text",
text: "!",
time: 2000,
isShowMask: true
});

self.$el.querySelector(".load-more").style.display = "none";
} else {

self.page = rs.number + 1;
self.size = rs.size;

let projects = rs.content;

self.rows = self.rows.concat(projects);
}
done(); 
},
function(response) {

self.$vux.loading.hide();
self.$el.querySelector(".load-more").style.display = "none";
self.$el.querySelector("#load-msg").style.display = "block";
okapp.alert({ message: response.errorDesc });
}
);
},
doBack() {
this.$router.go(-1);
},
doClose() {
okapp.closeMinApp();
},
/**
* 
*/
doAuthentication() {

okapp.getUserInfo(this.getUserInfoCallBack);
},
/**
* 
*/
getUserInfoCallBack(user) {
this.$vux.loading.show({
text: ",..."
});

if (null == user || typeof user == "undefined") {

this.$vux.loading.hide();
okapp.alert({ message: "!" });

okapp.closeMinApp();
return;
}

if (user.loginType != "cert") {

this.$vux.loading.hide();
okapp.alert({ message: "APP!" });

okapp.closeMinApp();
return;
}










var caUserInfo = {
CERT_INFO: user.cert,
USER_INFO: user.user,
OKAPP_OPEN_ID: user.openID
};


this.$eokb.setOKAppUser(caUserInfo);

this.login(caUserInfo);
},
/**
* 
*/
login(userInfo) {
var param = userInfo.CERT_INFO;
param.OKAPP_OPEN_ID = userInfo.OKAPP_OPEN_ID;
param = Object.assign(param, userInfo.USER_INFO);
console.log("");
let opts = {
method: "post",
data: param,
headers: {
"Content-Type": "application/x-www-form-urlencoded;charset=utf-8"
}
};
let url = this.$eokb.getRegionRoomURL() + "/mobile/auth";
var self = this;

okapp.requestExt(
url,
opts,
function(response) {

self.$vux.loading.hide();


var result = null;
try {
result = JSON.parse(response.data);
} catch (e) {
result = response.data;

}

if (result.success) {
var authToken = result.result.AUTH_TOKEN;

var user = result.result.CURRENT_USER;


self.$eokb.setAuthzToken(authToken);

self.$eokb.setCurrentUserInfo(user);
self.$router.push("/tpinfo");
return;
}
okapp.alert({ message: "!" });
},
function(response) {

self.$vux.loading.hide();
var errorJson = {};
try {
errorJson = JSON.parse(response.error);
} catch (e) {

if (-1 == response.status) {
errorJson = {
errorCode: "-1",
errorDesc: ",!"
};
} else if (1 == response.status) {
errorJson = { errorCode: "1", errorDesc: ",!" };
} else {
errorJson = {
errorCode: "0",
errorDesc: ",!"
};
}
console.error("JSON parsing error");
} finally {
okapp.alert({ message: errorJson.errorDesc });
}
}
);
}
},
components: {
FormPreview,
Scroll,
XHeader,
ViewBox,
LoadMore
},
directives: {
TransferDom
}
};



