
import XTable from 'vux/src/components/x-table/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
components: {
XTable,
XDialog,
Icon,
XButton,
ViewBox,
Divider
},
data() {
return {
bidGroups: [],
show: false,
showConfirmButton: null,

currentNodeID: null,

currentNodeName: null,

currentNodeStatus: null,

showTip: null,

countDowmTimer: null,

confirmTime: null
};
},
mounted: function() {

clearInterval(this.countDowmTimer);

this.onResume();
this.getElectronicsList();

this.getConfirmStatus();

this.currentNodeID = this.$route.query.nid;

this.currentNodeName = this.$route.query.nodeName;

this.currentNodeStatus = this.$route.query.nodeStatus;
},

destroyed: function() {

document.removeEventListener("resume", this.refreshCountDownContent);

okapp.unsubscribe("racloud_callback");
okapp.unsubscribe("racloud_cancel");
},
methods: {

onResume() {


document.addEventListener("resume", this.refreshCountDownContent);
},

refreshCountDownContent() {
this.showTip = false;
if (null != this.countDowmTimer) {

clearInterval(this.countDowmTimer);
this.countDowmTimer = null;
}

this.getConfirmStatus();
},
getElectronicsList() {
var self = this;
self.$vux.loading.show({
text: "..."
});
const options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID()
}
};
this.$request.sendRequest(
"/mobile/bidder/electronicslist",
options,
function(rs, response) {
self.$vux.loading.hide();
self.bidGroups = rs;
}
);
},
showElectronicsDetails(groupCode) {
this.$router.push("/gsgl/electronicsinfo/" + groupCode);
},
doConfirm(status, bidderID, groupCode) {
let self = this;
if (2 == status) {
self.$vux.confirm.prompt("", {
title: "",
confirmText: "",
cancelText: "",


onCancel() {

},
onConfirm(val) {
if (typeof val == "undefined" || val == null || val == "") {
alert("");
return;
}
val = "" + val;
self.confirm(status, bidderID, val);
}
});
} else {
self.confirm(status, bidderID, "");
}
},
confirm(status, bidderID, remark) {
let self = this;
self.$vux.loading.show({
text: "..."
});
let options = {
method: "post",
data: {
status: status.toString(),
tpID: self.$eokb.getTenderProjectID(),
modelType: "DYXF_electronics",
bidderID: bidderID,
remark: remark
}
};
self.$request.sendRequest("/mobile/modelCheck/confirm", options, function(
rs,
response
) {
var contennt = "";
if (status == 2) {
contennt = "";
}
var message = self.$eokb.getCurrentUserInfo().V_NAME + "-" + contennt;
var param = new Object();
param.nodeID = self.currentNodeID;
param.title = self.currentNodeName;
param.msg = message;

self.$im.sendReplyTendererCommond(param);
self.$router.push("/turningPage");
});
},
getConfirmStatus() {
var self = this;
self.$vux.loading.show({
text: "..."
});
let options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID(),
modelType: "DYXF_electronics"
}
};
self.$request.sendRequest("/mobile/modelCheck/status", options, function(
rs,
response
) {
self.$vux.loading.hide();

self.showTip = rs.success;
self.confirmTime = dateFormat(
rs.result.N_CONFIRM_TIME,
"YYYY-MM-DD HH:mm:ss"
);

if (
rs.result.NOW_TIME < rs.result.N_CONFIRM_TIME &&
self.currentNodeStatus == 2
) {
self.countdowm(rs.result.NOW_TIME, rs.result.N_CONFIRM_TIME);
}
});
},

countdowm(start, end) {
let self = this;
let timer = setInterval(function() {
let t = end - start;


start = start + 1000;
if (t <= 0) {
clearInterval(self.countDowmTimer);
self.$command.flushFlowPage();
}
}, 1000);
}
}
};



