
import XTable from 'vux/src/components/x-table/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'


export default {
components: {
XTable,
FormPreview
},
data() {
return {
benchmarkInfo: {},
items: []
};
},
mounted: function() {
this.getBenchmarkInfo();
},
methods: {
getBenchmarkInfo() {
var self = this;
self.$vux.loading.show({
text: "..."
});
const options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID(),
sectionID: self.$route.params.sectionID
}
};
this.$request.sendRequest("/mobile/benchmark/info", options, function(
rs,
response
) {

self.$vux.loading.hide();
self.benchmarkInfo = rs;
self.setitems(rs);
});
},
setitems(benchmark) {
let method = benchmark.METHOD;
let evaluation = benchmark.EVALUATION;
let obj = new Object();


if (1 == method) {
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_AVG);
this.items[0] = obj;

obj = new Object();
obj.label = "";
obj.value = evaluation[0].N_DOWN;
this.items[1] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_EVALUATION_PRICE);
this.items[2] = obj;
}


if (2 == method) {
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_AVG);
this.items[0] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_CONTROL_WEIGHT);
this.items[1] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_WEIGHTING_AVG);
this.items[2] = obj;

obj = new Object();
obj.label = "";
obj.value = evaluation[0].N_DOWN;
this.items[3] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_EVALUATION_PRICE);
this.items[4] = obj;
}


if (3 == method) {
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_AVG);
this.items[0] = obj;

let index = 3;

if (benchmark.VALIDNUM <= 8) {
obj = new Object();
obj.label = "G";
obj.value = this.formatNum(evaluation[0].N_MAXPRICE);
this.items[1] = obj;

obj = new Object();
obj.label = "D";
obj.value = this.formatNum(evaluation[0].N_MINPRICE);
this.items[2] = obj;

obj = new Object();
obj.label = "GD";
obj.value = this.formatNum(evaluation[0].N_AVGPRICE);
this.items[3] = obj;

index = 4;
}


if (9 <= benchmark.VALIDNUM <= 16) {
obj = new Object();
obj.label = "70%";
obj.value = this.formatNum(evaluation[0].N_SEVENTY_SUM);
this.items[1] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_MINPRICE);
this.items[2] = obj;
}

if (benchmark.VALIDNUM > 16) {
obj = new Object();
obj.label = "50%";
obj.value = this.formatNum(evaluation[0].N_FIFTY_SUM);
this.items[1] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_MINPRICE);
this.items[2] = obj;
}
obj = new Object();
obj.label = "";
obj.value = evaluation[0].N_DOWN;
this.items[index] = obj;

index++;
obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_EVALUATION_PRICE);
this.items[index] = obj;
}


if (4 == method) {
let index = 3;

if (benchmark.VALIDNUM <= 10) {
obj.label = "G";
obj.value = this.formatNum(evaluation[0].N_MAXPRICE);
this.items[0] = obj;

obj = new Object();
obj.label = "D";
obj.value = this.formatNum(evaluation[0].N_MINPRICE);
this.items[1] = obj;

obj = new Object();
obj.label = "GD";
obj.value = this.formatNum(evaluation[0].N_AVG);
this.items[2] = obj;
}


if (benchmark.VALIDNUM > 10) {
obj.label = "8";
obj.value = this.formatNum(evaluation[0].N_EIGHT_PRICE);
this.items[0] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_AVG);
this.items[1] = obj;
index = 2;
}

obj = new Object();
obj.label = "";
obj.value = evaluation[0].N_DOWN;
this.items[index] = obj;

index++;
obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_EVALUATION_PRICE);
this.items[index] = obj;
}


if (5 == method) {
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_ALLPRICE_AVG);
this.items[0] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[1].N_DU_PRICE_AVG);
this.items[1] = obj;

obj = new Object();
obj.label = "";
obj.value = evaluation[0].N_DOWN;
this.items[2] = obj;

obj = new Object();
obj.label = "";
obj.value = this.formatNum(evaluation[0].N_EVALUATION_PRICE);
this.items[3] = obj;
}
},
formatNum(num) {
return numberComma(Math.round(num));
}
}
};



