
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
components: {
Flexbox,
FlexboxItem,
Divider
},
data() {
return {

notices: [],

lastTimeStamp: 0,

updateNoticeCount: 0
};
},
mounted() {
this.getTPNotices();

this.$command.setNoticePage(this);
this.$command.setCurrentPage(this.$command.NOTICE_PAGE);
},
methods: {

getTPNotices() {
var self = this;

let url = "/mobile/notice/" + this.$eokb.getTenderProjectID();
this.$request.doGet(url, function(rs, response) {

self.$command.clearMainNoticeCount();

if (null == rs || rs.length < 1) {

self.$vux.toast.show({
type: "text",
text: "!",
time: 2000,
isShowMask: true
});
return;
}

self.lastTimeStamp = rs[rs.length - 1].N_CREATE_TIME;
self.notices = rs;
});
}
}
};



