
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
data() {
return {

unreadMsgCount: ""
};
},
components: {
Tab,
TabItem
},
mounted() {

this.$command.setIMPage(this);




this.$command.setCurrentPage(this.$command.MESSAGE_PAGE);
},
methods: {
onTabItemClick(index) {

if (0 == index) {
this.$router.push("/im");
return;
}

if (1 == index) {
this.$router.push("/participant");
return;
}
}
}
};



