
import FormPreview from 'vux/src/components/form-preview/index.vue'


export default {
components: {
FormPreview
},
data() {
return {
bidderInfos: [],
bidderName: ""
};
},
mounted: function() {
this.getBidderInfos();
},
methods: {
getBidderInfos() {
var self = this;
self.$vux.loading.show({
text: "..."
});
const options = {
method: "get",
params: {
bidderID: self.$route.query.bidderID,
index: self.$route.query.index,
tableName: self.$route.query.tableName
}
};
this.$request.sendRequest("/mobile/bidder/info", options, function(
rs,
response
) {
self.$vux.loading.hide();
self.bidderInfos = rs.CB_LIST;
self.bidderName = rs.V_BIDDER_NAME;
});
}
}
};



