
import FormPreview from 'vux/src/components/form-preview/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from "./Scroller";
import axios from "axios";
import session from '@/js/session.js'
export default {
data() {
return {
project: {},
buttons1: [
{
style: "primary",
text: "",
onButtonClick: name => {
this.goProject();
}
}
],
buttons2: [
{

style: "primary",
text: "",
onButtonClick: name => {
this.$router.push({
path: `/signin`
});
}
}
]
};
},
mounted: function() {
this.getProject();
},
methods: {
goProject() {
var self = this;
self.$request.doGet("/mobile/project/into/" + this.$eokb.getTenderProjectID(), function(
rs,
response
) {

self.$router.push({
path: "/main"
});
});
},
getProject() {
var self = this;
self.$vux.loading.show({
text: "..."
});

const options = {
method: "get",
params: { tpID: self.$eokb.getTenderProjectID() }
};
this.$request.sendRequest(
"/mobile/project/info",
options,
function(rs, response) {

self.$vux.loading.hide();

self.project = rs;
self.$eokb.setTenderProjectInfo(rs);

}
);
},
doBack() {
this.$router.go(-1);
},
doClose() {
this.$router.go(-1);
}
},
components: {
FormPreview,
Scroll,
XHeader,
ViewBox
},
directives: {
TransferDom
}
};



