
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from "axios";
export default {
components: {
Group,
XInput,
XButton
},
data() {
return {
disabled: true,
V_BIDDER_NAME: "",
V_BIDDER_PHONE: ""
};
},
mounted: function() {

},











methods: {
submit() {
var self = this;



const options = {
method: "post",
data: {
tpID: self.$eokb.getTenderProjectID(),
V_BIDDER_NAME: self.V_BIDDER_NAME,
V_BIDDER_PHONE: self.V_BIDDER_PHONE
}
};
self.$request.sendRequest(
"/mobile/signin/phone",
options,
function(rs, response) {
self.$vux.toast.show({
text: response.errorDesc,
time: 1000,
isShowMask: true,
onHide() {
self.$router.go(-1);
}
});
},
function(response) {
self.$vux.toast.show({
type: "warn",
text: response.errorDesc,
time: 2000,
isShowMask: true,
onHide() {
self.$router.go(-1);
}
});
}
);
},

keyDown() {
this.disabled = !(
this.$refs.V_BIDDER_NAME.valid && this.$refs.V_BIDDER_PHONE.valid
);
}
}
};



