
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

export default {
components: {
FormPreview,
Flow,
FlowState,
FlowLine
},
data() {
return {
nodeList: [],
infos: [],
projectName: ""
};
},
mounted: function() {
this.getProjectFlowList();
this.$command.setCurrentPage(this.$command.FLOW_PAGE);
},
beforeDestroy() {
this.$command.setCurrentPage(this.$command.OTHER_PAGE);
},
methods: {
getProjectFlowList() {
var self = this;

self.$vux.loading.show({
text: "..."
});

const options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID()
}
};
self.$request.sendRequest("/mobile/tpflow/list", options, function(
rs,
response
) {

self.$vux.loading.hide();

self.nodeList = rs.NODE_LIST;
self.infos = rs.INFOS;
self.projectName = rs.PROJECT_NAME;
});
},
openNode(path, nid, nodeName, nodeStatus) {

if ("undefined" == typeof path || null == path || "" == path) {
return;
}

this.$router.push({
path: path,
query: {

nid: nid,
nodeName: nodeName,
nodeStatus: nodeStatus
}
});
}
}
};



