
import XTable from 'vux/src/components/x-table/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
components: {
XTable,
Divider
},
data() {
return {
sections: [],
showStatus: true,
isKCSJ: true
};
},
mounted: function() {
this.getReviewlist();
},
methods: {
getReviewlist() {
var self = this;
self.$vux.loading.show({
text: "..."
});
const options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID()
}
};
this.$request.sendRequest("/mobile/bidder/reviewlist", options, function(
rs,
response
) {
self.$vux.loading.hide();
self.sections = rs.SECTION_lIST;
self.showStatus = rs.IS_PB_OVER;
self.isKCSJ = rs.IS_KCSJ;
});
}
}
};



