
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
XButton,
Flexbox,
FlexboxItem
},
mounted() {

this.msgPromptTone =
this.$eokb.getMobileAppHome() + "/static/audio/msg.mp3";

this.isBidder = this.$eokb.isBidder();

this.currentUserID = this.$eokb.getCurrentUserID();

this.$command.setMessagePage(this);
this.$command.setCurrentPage(this.$command.MESSAGE_PAGE);























this.$command.popUnreadMessage();

this.getHistoricalChatDatas();

this.getMeetingRoomTenderer();
},
data() {
return {

isBidder: true,

readMsgs: [],

appendMsgs: [],

currentUserID: "",

cantSendMsg: true,

recipientID: null,

recipientName: null,

message: null,

msgPromptTone: "/static/audio/msg.mp3"
};
},
methods: {
/**
* 
*/
getHistoricalChatDatas() {
var self = this;

let url = "/mobile/im/hcdatas/" + this.$eokb.getTenderProjectID();
this.$request.doGet(url, function(rs, response) {

if (null == rs || rs.length < 1) {
return;
}
for (var i = 0; i < rs.length; i++) {
rs[i].time = self.$eokb.formatTimeStamp(rs[i].N_SEND_TIME);
}
self.readMsgs = rs;
self.scrollToBottom();
});
},

/**
* 
*/
receiveMsg(msg) {
msg.time = this.$eokb.formatTimeStamp(msg.SEND_TIME);
this.appendMsgs.push(msg);
this.hintMsg();
this.scrollToBottom();
},
/**
* 
*/
getMeetingRoomTenderer() {
var self = this;

let url = "/mobile/im/roomtder/" + this.$eokb.getTenderProjectID();
this.$request.doGet(url, function(rs, response) {

self.cantSendMsg = !rs.success;
if (null == rs || !rs.success) {

self.$vux.toast.show({
type: "text",
text: "/,!",
time: 2000,
isShowMask: true
});
return;
}
var tenderder = rs.result;

if (self.currentUserID == tenderder.USER_ID) {
return;
}


self.recipientID = tenderder.USER_ID;
self.recipientName = tenderder.USER_NAME;
});
},
/**
* 
*/
sendMessage() {

this.$im.sendChatMessage(
this.recipientID,
this.recipientName,
this.message
);
this.message = "";
},

hintMsg() {
this.$eokb.getMute()
? this.$refs.msgPromptToneAudio.pause()
: this.$refs.msgPromptToneAudio.play();
},

scrollToBottom() {
setTimeout(() => {

var currentDistance =
this.$refs.msgContentBox.scrollHeight -
this.$refs.msgContentBox.clientHeight;

var currentScroll_y = this.$refs.msgContentBox.scrollTop;
if (currentDistance > 0 && currentDistance > currentScroll_y) {
currentScroll_y =
Math.ceil((currentDistance - currentScroll_y) / 10) +
currentScroll_y;
currentScroll_y =
currentScroll_y > currentDistance
? currentDistance
: currentScroll_y;


this.$refs.msgContentBox.scrollTop = currentScroll_y;
this.scrollToBottom();
}
}, 13);
}
}
};



