// @ == src
import command from '@/js/command.js'
import eokb from '@/js/eokb.js'
const im = {

  /**
   * WebSocket
   */
  ws: null,

  /**
   * WebSocket 
   * @param {ws} ws 
   */
  setWebSocket(ws) {
    this.ws = ws
  },

  /**
   * WebSocket
   */
  getWebSocket() {
    if (this.ws != null && typeof (this.ws) != 'undefined' &&
      this.ws.readyState !== WebSocket.CLOSED) {
      return this.ws
    }
    return null
  },

  /**
   * 
   * @param {} msg 
   */
  onMessage(e) {
    var data = e.data
    if (data == null && typeof (data) == 'undefined') {
      return
    }
    console.log(data)
    // JSON
    var response = JSON.parse(data)
    // 
    var type = response.MESSAGE_TYPE
    // web
    if (1 == type) {
      return
    }
    // 
    if (100 == type || -4 == type) {
      // 
      this.excuteCommond(response)
      return
    }
    // 
    if (-1 == type) {
      this.refreshNotice(response)
      return
    }
    // 
    if (0 == type) {
      this.refreshChatMessage(response)
      return
    }
    okapp.alert({message: ':' + msg})
  },

  /**
   * 
   * @param {} cmd 
   */
  excuteCommond(cmd) {
    command.excute(cmd)
  },

  /**
   * 
   * @param {} msg 
   */
  refreshChatMessage(msg) {
    command.refreshMessage(msg)
  },

  /**
   * 
   * @param {} notice 
   */
  refreshNotice(notice) {
    command.refreshNotice(notice)
  },

  /**
   * 
   * @param {ID,NULL} recipientID 
   * @param {,NULL} recipientName 
   * @param {} msg 
   */
  sendChatMessage(recipientID, recipientName , msg) {
    if (null == msg || typeof (msg) == 'undefined' || msg == '') {
      okapp.alert({message: '!'})
      return
    }
    if (null == recipientID || typeof (recipientID) == 'undefined') {
      recipientID = ''
    }
    if (null == recipientName || typeof (recipientName) == 'undefined') {
      recipientName = ''
    }
    var message = {}
    message.MESSAGE_CONTENT = msg
    message.RECIPIENT_ID = recipientID
    message.RECIPIENT_NAME = recipientName
    message.MESSAGE_TYPE = 0
    this.ws.send(JSON.stringify(message))
  },

  /**
   * ,
   * @param {} params 
   */
  sendReplyTendererCommond(params) {
    // 
    if (!eokb.isBidder()) {
      okapp.alert({message: '!'})
      return
    }
    var param = {}
    param.oneParam = params
    param.fun = 'respCommandCallback'
    var message = {}
    message.RECIPIENT_ID = ''
    message.RECIPIENT_NAME = ''
    message.MESSAGE_TYPE = -4
    message.MESSAGE_CONTENT = JSON.stringify({
      'COMMAND': 'responseReplyTendererCommond',
      'PARAM': param
    })
    this.ws.send(JSON.stringify(message))
  },

  /**
   * WEB
   * @param {} param 
   */
  sendCommondToWEB(param) {
    var message = {}
    message.RECIPIENT_ID = ''
    message.RECIPIENT_NAME = ''
    message.MESSAGE_TYPE = 1
    message.MESSAGE_CONTENT = JSON.stringify({
      'COMMAND': 'commondCallback',
      'PARAM': param
    })
    this.ws.send(JSON.stringify(message))
  },

  /**
   * 
   * @param {} param 
   */
  sendCommondToMobile(param) {
    var message = {}
    message.RECIPIENT_ID = ''
    message.RECIPIENT_NAME = ''
    message.MESSAGE_TYPE = 100
    message.MESSAGE_CONTENT = JSON.stringify({
      // 
      'COMMAND': 'excuteParamCommand',
      'PARAM': param
    })
    this.ws.send(JSON.stringify(message))
  },

  /**
   * WEB
   * @param {} param 
   */
  sendCommond(param) {
    try {
      // 
      this.sendCommondToMobile(param)
    } catch (e) {
      console.log(e)
    }
    try {
      // WEB
      this.sendCommondToWEB(param)
    } catch (e) {
      console.log(e)
    }
  }

}

// vue
export default im



