
import XTable from 'vux/src/components/x-table/index.vue'


export default {
components: {
XTable
},
data() {
return {
electronicsInfo: []
};
},
mounted: function() {
this.getElectronicsInfo();
},
methods: {
getElectronicsInfo() {
var self = this;
self.$vux.loading.show({
text: "..."
});
const options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID(),
groupCode: self.$route.params.groupCode
}
};
this.$request.sendRequest(
"/mobile/bidder/electronicsinfo",
options,
function(rs, response) {
self.$vux.loading.hide();
self.electronicsInfo = rs;
}
);
}
}
};



