import Vue from 'vue'
import Router from 'vue-router'
import E404 from '@/components/404'
import Main from '@/components/Main'
import ProjectList from '@/components/ProjectList'
import ProjectInfo from '@/components/ProjectInfo'
import TurningPage from '@/components/TurningPage'


import Signin from '@/components/common/Signin'
import ProjectFlow from '@/components/common/ProjectFlow'
import BidderList from '@/components/common/BidderList'
import BidderInfo from '@/components/common/BidderInfo'
import DecryptBidDoc from '@/components/common/DecryptBidDoc'

import Benchmark from '@/components/gsgl/Benchmark'
import BenchmarkInfo from '@/components/gsgl/BenchmarkInfo'
import Credit from '@/components/gsgl/Credit'
import Electronics from '@/components/gsgl/Electronics'
import ElectronicsInfo from '@/components/gsgl/ElectronicsInfo'
import Review from '@/components/gsgl/Review'


import IM from '@/components/im/IM'
import Message from '@/components/im/Message'
import Participant from '@/components/im/Participant'


import Notice from '@/components/notice/Notice'


import CurrentUserInfo from '@/components/me/CurrentUserInfo'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.use(ConfirmPlugin)
Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)

Vue.use(Router)

export default new Router({

routes: [
{
path: '/',
name: 'projectList',
component: ProjectList
},
{
path: '/tpinfo',
name: 'ProjectInfo',
component: ProjectInfo
},
{
path: '/signin',
name: 'Signin',
component: Signin
},
{
path: '/main',
component: Main,
children: [
{
path: '/',
component: ProjectFlow
},
{
path: '/im',
component: IM,
children: [
{
path: '/',
component: Message
},
{
path: '/participant',
component: Participant
}
]
},
{
path: '/decrypt',
name: 'DecryptBidDoc',
component: DecryptBidDoc
},
{
path: '/notice',
name: 'Notice',
component: Notice
},
{
path: '/me',
name: 'CurrentUserInfo',
component: CurrentUserInfo
},
{
path: '/bidderlist/:index',
component: BidderList
},
{
path: '/gsgl/credit/:index',
component: Credit
},
{
path: '/gsgl/electronics',
component: Electronics
},
{
path: '/gsgl/electronicsinfo/:groupCode',
component: ElectronicsInfo
},
{
path: '/gsgl/review',
component: Review
},
{
path: '/bidderinfo',
component: BidderInfo
},
{
path: '/gsgl/benchmark',
component: Benchmark
},
{
path: '/gslg/benchmarkinfo/:sectionID',
component: BenchmarkInfo
},
{
path: '/turningPage',
component: TurningPage
}
]
},
{
path: '*', // *,
component: E404
}
]
})



