
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
FormPreview,
Tab,
TabItem,
Divider,
Group,
Cell
},
data() {
return {
userInfos: [],
keyName: "",
envelopes: []
};
},
mounted() {

let user = this.$eokb.getOKAppUser();
this.keyName = user.CERT_INFO.V_KEY_NAME;
let userInfo = new Object();
userInfo.label = "";
userInfo.value = user.USER_INFO.V_NAME;
this.userInfos[0] = userInfo;

userInfo = new Object();
userInfo.label = "";
userInfo.value = user.USER_INFO.V_SOCIALCREDIT_NO;
this.userInfos[1] = userInfo;

userInfo = new Object();
userInfo.label = "";
userInfo.value = user.USER_INFO.V_COMPANY_CODE;
this.userInfos[2] = userInfo;


this.getUserInfos();
},
methods: {
getUserInfos() {
var self = this;

self.$vux.loading.show({
text: "..."
});

const options = {
method: "get",
params: {
tpID: self.$eokb.getTenderProjectID()
}
};
self.$request.sendRequest("/mobile/user/info", options, function(
rs,
response
) {

self.$vux.loading.hide();
self.envelopes = rs;

});
},
showDetail(ID, index) {
this.$router.push({
path: "/bidderinfo",
query: {
bidderID: ID,
index: index.toString(),
tableName: "EKB_T_TENDER_LIST"
}
});
},
isEmpty(params) {
var arr = Object.keys(params);
return arr.length == 0;
}
}
};



