
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
components: {
Flexbox,
FlexboxItem,
Divider
},
data() {
return {

participants: [],

isTenderer: false,

current: null,

websocket: null
};
},
mounted() {
this.getParticipants();

this.$command.setParticipantPage(this);
this.$command.setCurrentPage(this.$command.PARTICIPANT_PAGE);
},
methods: {

getParticipants() {
this.websocket = this.$im.getWebSocket();
this.current = this.$session.getObject("currentParticipant");

this.isTenderer = this.$session.getValue("IS_TENDERER");
var self = this;

let url = "/mobile/im/participants/" + this.$eokb.getTenderProjectID();
this.$request.doGet(url, function(rs, response) {

if (null == rs || rs.length < 1) {

self.$vux.toast.show({
type: "text",
text: "!",
time: 2000,
isShowMask: true
});
return;
}
for (var i = 0; i < rs.length; i++) {
rs[i].time = self.$eokb.formatTimeStamp(rs[i].JOIN_TIME);
}
self.participants = rs;
});
}
}
};



