
import Vue from "vue";
import "video.js/dist/video-js.css";
import "vue-video-player/src/custom-theme.css";
import { videoPlayer } from "vue-video-player";
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
data() {
return {

websocket: null,

connWSTimes: 0,

isConnected: false,
menus: {
refresh: "",
setting: "",
video: "",
exit: "",
close: ""
},

msgCount: "",

noticeCount: "",
showMenus: false,

msgPromptToneMp3: "/static/audio/msg.mp3",

onLinePromptToneMp3: "/static/audio/online.mp3",

showSettingPopup: false,

mute: false,

videoList: [],

checkedVideo: [],

showVideoListPopup: false,

showVideoDialog: false,

playerOptions: {
playbackRates: [0.7, 1.0, 1.5, 2.0], //
controls: true, //
autoplay: false, //true,
muted: false, // 
loop: false, // 
preload: "auto", // <video>auto,
language: "zh-CN",
aspectRatio: "4:3", //  - "16:9""4:3"
fluid: true, // trueVideo.js player
sources: [
{

type: "application/x-mpegURL"

}
],


notSupportedMessage: "" 
}
};
},
mounted() {

this.msgPromptToneMp3 =
this.$eokb.getMobileAppHome() + "/static/audio/msg.mp3";

this.onLinePromptToneMp3 =
this.$eokb.getMobileAppHome() + "/static/audio/online.mp3";

var ws = this.$im.getWebSocket();
if (null != ws && typeof ws != "undefined") {
ws.close();
}
this.doVerifyIMConnection();

this.$command.setMainPage(this);


this.mute == this.$eokb.getMute();

let videos = this.$eokb.getVideos();
if (videos.length > 0) {
for (let i = 0; i < videos.length; i++) {
let v = { key: videos[i].src, value: videos[i].name };
this.videoList.push(v);
}
}
},
computed: {
player() {
return this.$refs.videoPlayer.player;
}
},
destroyed() {

this.websocket.close();

this.player.dispose();
},
methods: {
doBack() {
this.$router.go(-1);
},
onClickMenu(menuKey, menuItem) {

if ("refresh" == menuKey) {
this.doRefresh();
return;
}
if ("close" == menuKey) {
this.doClose();
return;
}

if ("video" == menuKey) {




if (null != this.videoList && 0 < this.videoList.length) {
this.showVideoListPopup = true;
return;
}
this.$vux.toast.show({
type: "text",
text: "!",
time: 3000,
isShowMask: true
});
return;
}

if ("exit" == menuKey) {
this.$router.push("/");
return;
}

if ("setting" == menuKey) {
this.showSettingPopup = true;
return;
}
},
doRefresh() {
this.$router.push("/turningPage");
},
doClose() {
okapp.closeMinApp();
},

onPlayerDialogClose() {

this.player.pause();
},

onPlayerLoadeddata(e) {


},

onChangeVideo(src, name) {
if (null == src || src == "" || typeof src == "undefined") {
return;
}
console.log("change", src, name);

var posterURL = this.$eokb.getMobileAppHome() + "/static/img/poster.png";
this.player.poster(posterURL);

this.player.src(src);

this.showVideoListPopup = false;

this.showVideoDialog = true;

this.player.play();
},
doVerifyIMConnection() {

var self = this;

let url = "/mobile/im/room/" + this.$eokb.getTenderProjectID();

this.$request.sendRequest(
url,
null,
function(rs, response) {

self.$eokb.setWebSocketURL(rs.result.WEBSOCKET_URL);
self.$session.setObject("currentParticipant", rs.result.CURRENT_USER);
self.$session.setValue("IS_TENDERER", rs.result.IS_TENDERER);

self.initWebSocket();
},
function(rs, error) {

self.$vux.toast.show({
type: "text",
text: rs.errorDesc,
time: 2000,
isShowMask: true
});

self.$router.push("/");
}
);
},

isConnectedWS() {
var ws = this.$im.getWebSocket();
if (null != ws && typeof ws != "undefined") {
this.websocket = ws;
return true;
}
return (
(null != this.websocket &&
typeof this.websocket != "undefined" &&
this.websocket.readyState !== WebSocket.CLOSED) ||
this.isConnected
);
},

initWebSocket() {
if (this.isConnectedWS()) {
this.connWSTimes = 1;
this.isConnected = true;
return;
}

const wsuri = this.$eokb.getWebSocketURL();
console.log("WebSocket" + wsuri);
this.websocket = new WebSocket(wsuri); 
this.websocket.onopen = this.webSocketOnOpen;
this.websocket.onmessage = this.webSocketOnMessage;
this.websocket.onclose = this.webSocketOnClose;
this.websocket.onerror = this.webSocketOnError;
this.connWSTimes++;
},
webSocketOnOpen() {

console.log("WebSocket");

this.$vux.toast.show({
type: "text",
text: "!",
time: 1000,
isShowMask: true
});

this.$im.setWebSocket(this.websocket);
this.connWSTimes = 1;
this.isConnected = true;
this.$vux.loading.hide();
},
webSocketOnMessage(e) {

this.$im.onMessage(e);
},
webSocketOnClose() {

console.log("WebSocket");
this.isConnected = false;

if (this.connWSTimes > 10) {
this.connWSTimes = 999;

this.$vux.toast.show({
type: "text",
text: "!",
time: 3000,
isShowMask: true
});

this.$router.push("/");
return;
}

this.$vux.loading.show({
text: "..."
});
let time = this.connWSTimes * 1000;
setTimeout(this.initWebSocket, time);
},
webSocketOnError() {

console.log("WebSocket");
this.$vux.loading.hide();

this.$vux.toast.show({
type: "text",
text: "!",
time: 2000,
isShowMask: true
});

this.$router.go(-1);
},

hintMsg() {
this.$eokb.getMute()
? this.$refs.msgPromptToneAudio.pause()
: this.$refs.msgPromptToneAudio.play();
},

hintOnline() {
this.$eokb.getMute()
? this.$refs.onLinePromptToneAudio.pause()
: this.$refs.onLinePromptToneAudio.play();
},

setMute(value) {
this.mute = value;
this.$eokb.setMute(this.mute);
}
},
directives: {
TransferDom
},
components: {
XHeader,
Tabbar,
TabbarItem,
Actionsheet,
Popup,
XSwitch,
Group,
XButton,
XDialog,
ViewBox,
Checklist,
videoPlayer
}
};



